<template>
  <div class="setting-wrapper">
      <yd-cell-group title="结合Cell组件" class="demo-small-pitch">
            <yd-cell-item type="label">
                <span slot="left">值为：</span>

                <yd-switch slot="right" true-value=></yd-switch>

            </yd-cell-item>
            <p slot="bottom" style="text-align:right;padding: .05rem .24rem;color:#555;">设置属性type为label即可点击整行控制switch组件</p>
        </yd-cell-group>

        <yd-accordion>
            <yd-accordion-item title="李白《月下独酌》">
                <div style="padding: .24rem;">
                    <p>花间一壶酒，独酌无相亲。</p>
                    <p>举杯邀明月，对影成三人。</p>
            
                </div>
            </yd-accordion-item>
            
        </yd-accordion>
  </div>
</template>

<script>
import {Cell, Switch as Vswitch , Accordion} from 'vue-ydui'
export default {
  data () {
    return {
        gender:[],
        switch1: false,
        switch2: true,
        switch3: true,
        switch4: 'aaaaa',
        switch5: true,
    }
  },
  components: {
      Cell,
      Vswitch,
      Accordion
  }
}
</script>

<style lang="stylus" scoped>

</style>




